<style>
a{
  text-decoration: none;
}
a:hover{
  text-decoration: none;
}

</style>

<template>
  <v-app>
    <v-app-bar
      app
      color="primary"
      dark
      src="https://z3.ax1x.com/2021/05/05/gKwkP1.png"
    >
      <template v-slot:img="{ props }">
        <v-img
          v-bind="props"
          gradient="to top right, rgba(19,84,122,.5), rgba(128,208,199,.8)"
        ></v-img>
      </template>
      <v-app-bar-title>
        <v-icon>
          mdi-emoticon-cool-outline
        </v-icon>
        Gengtu 提交
      </v-app-bar-title>
      
      <v-spacer></v-spacer>

      <a
        href="https://imgtu.com/"
        target="_blank"
      >
        <v-avatar
          v-ripple
        >
          <v-icon>
            mdi-snowflake
          </v-icon>
        </v-avatar>
      </a>
    </v-app-bar>

    <v-main 
      style="background: url('https://z3.ax1x.com/2021/05/05/gMQDqx.jpg') no-repeat;background-size: 100% 100%;"
    >
      <v-layout
        row
        wrap
        align-center
        justify-center
        fill-height
      >
        <template>
          <v-card
            class="mx-auto"
            max-width="80%"
            rounded="true"
            elevation="24"
            style="opacity: 0.7;"
          >
            <v-card-title>
              Gengtu 提交
            </v-card-title>
            <v-card-text>
              <v-alert
                border="left"
                colored-border
                type="warning"
                elevation="2"
              >
                本页面将指导您提交 Gengtu 。
                <br />
                注意：由于本页面实际上是带领您在 Github 上的 Gengtu 存储库提交一个 Issue ，所以您需要一个 Github账号 。而且， Github 有时可能无法访问，您需要 Cross Great Wall .
                <br />
                PS: 在描述里记住加个QQ号。
              </v-alert>

              <v-form
                ref="form"
                v-model="valid"
              >
                <v-textarea
                  label="图片链接"
                  :rules="imgLinkRule"
                  hide-details="auto"
                  ref="imgLink"
                  rows="1"
                  no-resize
                  value=" "
                >
                </v-textarea>

                <v-textarea
                  label="描述（可选项）"
                  auto-grow
                  rows="5"
                  row-height="45"
                  :rules="descRule"
                  counter="100"
                  hide-details="auto"
                  ref="descText"
                  value=" "
                ></v-textarea>
              
                <v-combobox
                  v-model="chips"
                  :items="items"
                  chips
                  clearable
                  label="与此 Gengtu 相关的人"
                  multiple
                  prepend-icon="mdi-human-greeting"
                  solo
                  :rules="peoplesSelectRule"
                  ref="peoplesSelect"
                >
                  <template v-slot:selection="{ attrs, item, select, selected }">
                    <v-chip
                      v-bind="attrs"
                      :input-value="selected"
                      close
                      @click="select"
                      @click:close="remove(item)"
                    >
                      <strong>{{ item }}</strong>&nbsp;
                    </v-chip>
                  </template>
                </v-combobox>
                <v-btn
                  :disabled="!valid"
                  color="success"
                  class="mr-4"
                  @click="validate"
                >
                  提交
                </v-btn>

                <v-btn
                  color="error"
                  class="mr-4"
                  @click="reset"
                >
                  重置
                </v-btn>
              </v-form>
            </v-card-text>
          </v-card>
        </template>
      </v-layout>
    </v-main>
  </v-app>
</template>

<script>

export default {

  data: () => ({
    valid: true,
    loading: false,
    imgLinkRule: [
      value => !!value && value!="https://exmaple.com/exmaple.png"|| '此字段必须填入。',
      value => /^((https|http)?:\/\/)[^\s]+[^\s]\.(png|jpg|jpeg)/.test(value) || '链接格式有误。请粘贴来自图床的图片直链。（可以点击应用栏的右边的冰雪图标跳转到路过图床）'
    ],
    descRule: [
      value => value.length <= 100 || '最多输入 100 个字。',
    ],
    peoplesSelectRule: [
      value => value.length != 0 || '必须选择与此 Gengtu 相关的人。'
    ],
    chips: ["空"],
    items: ["空", "霜啸", "HelloGwkki", "神性", "DXJ", "Kasight", "蓝梦", "宇宙", "彩虹", "NoName", "dundun"]
  }),
  methods: {
    remove (item) {
      this.chips.splice(this.chips.indexOf(item), 1)
      this.chips = [...this.chips]
    },
    validate () {
      this.$refs.form.validate();
      this.submitForm();
    },
    reset () {
      this.$refs.form.reset()
    },
    submitForm(){
      /*onsole.log(this.$children[0].$children[1].$children[1].$children[1]);*/ //This is a joke.
      window.location.replace("https://github.com/HelloGwkki/GengTu/issues/new?title=GengtuUpload&body=<!--DON'T EDIT.-->%0A%0A[IMAGE LINK]%0A" + this.$refs.imgLink.value + "%0A%0A[DESCRIPTION]%0A" + this.$refs.descText.value + "%0A%0A[PEOPLES]%0A" + this.$refs.peoplesSelect.value.join(" "));
    }
  },
};
</script>
